* {
  padding: 0;
  margin: 0;
}
html {
  height: 100%;
}
body {
  background: yellow;
  position: relative;
  font-family: "Roboto", sans-serif;
  height: 100%;
}
.wrapper {
  display: flex;
  flex-direction: column;
  height: 100%;
}
.site-content {
  flex: 1 0 auto;
}
.container,
.menu-container {
  max-width: 1200px;
  margin: 0 auto 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.container {
  align-items: unset;
}
.content {
  max-width: 90vw;
  text-align: justify;
}
.content h1 {
  text-align: center;
}
.content h3 {
  margin: 10px 20px;
}
.sidebar {
  max-width: 20vw;
  padding: 10px 20px;
  margin-left: 2vw;
}
.sidebar h3 {
  margin: 5px auto;
}
.sidebar select,
.sidebar input,
.sidebar #deleteP {
  width: 80%;
  margin: 5px 0;
  padding: 5px 5px;
}
.sidebar #deleteP {
  background: #3498db;
  padding: 5px 0;
  border-radius: 15px;
  color: white;
}
.logo img {
  height: 100px;
}
.menu {
  list-style-type: none;
  display: flex;
  flex-direction: row;
  align-items: center;
}
.menu li {
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande", "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
}
.menu a {
  padding: 20px;
  text-align: center;
  display: block;
  text-decoration: none;
  color: black;
}
#error {
  text-align: center;
  font-size: 25px;
  color: black;
  background: red;
  margin: 10px 0;
}
.authmenu {
  list-style-type: none;
  display: flex;
  flex-direction: row;
  align-items: center;
  width: 200px;
}
.authmenu li {
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande", "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
}
.authmenu a {
  padding: 10px;
  text-align: center;
  display: block;
  text-decoration: none;
  color: black;
}
.authmenu li:first-child {
  margin-right: 1vw;
}
.menu li:hover {
  background: #e5e600;
}
.menu li:hover a {
  border-bottom: 1px solid black;
}
.Login {
  background: #97db0e;
  border-radius: 10px;
}
.Login:hover {
  background: #86c30c;
}
.Reg {
  background: #eb4e4e;
  border-radius: 10px;
}
.Reg:hover {
  background: #e83737;
}
#logOut {
  background: #eb4e4e;
  border-radius: 10px;
  border-radius: 20px;
  padding: 10px 20px;
  color: white;
}
#logOut:hover {
  background: #e83737;
}
footer {
  text-align: center;
  padding: 20px 0;
  background: #8c8c73;
  flex: 0 0 auto;
}
footer .social {
  margin: 10px auto;
  padding-top: 10px;
  width: 1200px;
}
footer .social a {
  color: black;
  text-decoration: none;
  margin: 20px;
}
footer .social a:hover {
  color: #ccc;
}
footer .social a i {
  font-size: 15px;
  transform: scale(2);
}
#modal-login,
#modal-reg {
  display: none;
  text-align: center;
  max-width: 200px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border: #ffbf86 2px solid;
  background: peachpuff;
  border-radius: 20px;
}
#modal-login form,
#modal-reg form {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 200px;
  margin: 20px 0 0;
  padding-top: 10px;
}
#modal-login form h3,
#modal-reg form h3 {
  margin-bottom: 5px;
}
#modal-login form input,
#modal-reg form input {
  margin: 10px 0;
  width: 85%;
  font-size: 15px;
  font-family: "Roboto", sans-serif;
  padding: 5px;
}
#modal-login form input:focus,
#modal-reg form input:focus {
  outline: #ffbf86 3px solid;
}
#modal-login button,
#modal-reg button {
  width: 80%;
  height: 30px;
  border-radius: 20px;
  background: #fafa96;
  border: #ffbf86 2px solid;
  margin: 10px 0;
  font-size: 15px;
  font-family: "Roboto", sans-serif;
}
#modal-login button.cancel,
#modal-reg button.cancel {
  margin-top: 0;
  margin-bottom: 20px;
}
#modal-login button#auth:hover,
#modal-reg button#auth:hover,
#modal-login button#register:hover,
#modal-reg button#register:hover {
  background: yellowgreen;
}
#modal-login button.cancel:hover,
#modal-reg button.cancel:hover {
  background: #eb4e4e;
}
@media screen and (max-width: 768px) {
  .container {
    flex-direction: column;
    margin: 5vw;
  }
  .content,
  .sidebar {
    max-width: 90vw;
  }
  #modal-login,
  #modal-reg {
    top: 50vh;
  }
  .menu-container {
    flex-direction: column;
    justify-content: center;
  }
  .menu-container li {
    margin-bottom: 10px;
  }
}
